.wrapper {
  width: 90%;
  /* margin: auto; */
  padding: 1rem 0;
  /* position: relative; */
}

.icon {
  margin: 0.8rem;
  position: absolute;
  pointer-events: none;
}

.input {
  width: 100%;
  /* position: relative; */
  height: 3rem;
  font-size: 1.5rem;
  color: #00474b;
  font-family: "Space Mono", monospace;
  border: none;
  border-radius: 0.5rem;
  outline-color: #26c2ae;
  padding-top: 0;
  margin-top: 0;
  /* appearance: none; */
}
::placeholder {
  color: #5e7a7d;
  opacity: 0.7;
  padding-right: 0.5rem;
}
.label {
  font-family: "Space Mono", monospace;
  font-size: 1rem;
  color: #5e7a7d;
  margin-bottom: 0;
  padding: 1.2rem 0;
}

.textContainer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

.warning {
  font-family: "Space Mono", monospace;
  /* margin-bottom: 0; */
  font-size: 1rem;
  padding: 0.5rem;
  color: red;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  appearance: none;
  margin: 0;
}

input[type="number"] {
  -moz-appearance: textfield;
  appearance: none;
}

@media screen and (max-width: 715px) {
  .wrapper {
    margin: auto;
  }
}
