.container {
  display: flex;
  align-items: center;
  width: 30%;
  height: 2.5rem;
  min-width: 3.5rem;
  margin-bottom: 0.5rem;
  justify-content: center;
  background-color: #00474b;
  color: white;
  font-family: "Space Mono";
  font-size: 1.5rem;
  border-radius: 0.3rem;
}
.container:hover {
  background-color: #9fe8df;
  color: #00474b;
}

.input {
  width: 100%;
  height: 2.5rem;
  font-size: 1.2rem;
  border: none;
  font-family: "Space Mono";
  border-radius: 0.3rem;
  outline-color: #26c2ae;
}
::placeholder {
  color: #5e7a7d;
  opacity: 0.7;
  /* padding-right: 0.5rem; */
}
