.container {
  padding-top: 1.5rem;
  width: 50%;
  background-color: hsl(183, 100%, 15%);
  border-radius: 1rem;
  display: flex;
  align-content: space-between;
  flex-direction: column;
  min-width: 300px;
}

.reset {
  background: #26c2ae;
  border-radius: 5px;
  width: 80%;
  height: 2.5rem;
  margin: auto;
  margin-bottom: 2rem;
  border-style: none;
  color: #00474b;
  font-weight: 700;
  font-size: 1rem;
  letter-spacing: 1px;
}

.reset:hover {
  background-color: #9fe8df;
}

@media screen and (max-width: 715px) {
  .container {
    width: 100%;
  }
}
