.container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 2.5rem 1rem;
}

.heading {
  padding-bottom: 0;
  margin-bottom: 0;
  font-family: "Space Mono";
  font-style: normal;
  font-weight: 700;
  font-size: 1rem;
  line-height: 1.5rem;
  color: #ffffff;
}

.person {
  margin-top: 0;
  color: #7f9d9f;
  line-height: 1.2rem;
  font-size: 0.8rem;
  font-weight: 700;
}

.total {
  font-family: "Space Mono";
  font-style: normal;
  font-weight: 700;
  font-size: 3rem;
  line-height: 4.5rem;
  /* text-align: right; */
  letter-spacing: -1px;
  color: #26c2ae;
}

@media screen and (max-width: 715px) {
  .container {
    padding: 0.5rem 1rem;
  }
}
